import React, { Component } from 'react';
import { Table , Tabs} from 'antd';
const TabPane = Tabs.TabPane;
class BetLists extends Component {
    render() {
        const columns = [{
            title: 'ID',
            dataIndex: 'id',
            key: 'id',
            render: text => <a href="#">{text}</a>,
          }, {
            title: ' ',
            dataIndex: 'user',
            key: 'user',
          }, {
            title: '',
            dataIndex: 'time',
            key: 'time',
          },
          {
            title: '',
            dataIndex: 'bet',
            key: 'bet',
          },
          {
            title: '',
            dataIndex: 'payout',
            key: 'payout',
          },
          {
            title: '',
            dataIndex: 'game',
            key: 'game',
          },
          {
            title: '',
            dataIndex: 'count',
            key: 'count',
          },
          {
            title: '',
            dataIndex: 'profit',
            key: 'profit',
            render: text => {
                if (parseFloat(text) > 0) {
                    return <span className="green">{text}</span>
                } else {
                    return <span className="red">{text}</span>
                }
            },
          }];
          const data = [{
            id: '20,169,927,283',
            user: 'John Brown',
            time: '23:57',
            bet: '0.00000001',
            payout: '3.00x',
            game: '< 33.00',
            count: '5.23',
            profit: '0.00000002',
            key: '1'
          }, {
            id: '20,169,927,283',
            user: 'John Brown',
            time: '23:57',
            bet: '0.00000001',
            payout: '3.00x',
            game: '< 33.00',
            count: '5.23',
            profit: '0.00000002',
            key: '2'
          }, {
            id: '20,169,927,283',
            user: 'John Brown',
            time: '23:57',
            bet: '0.00000001',
            payout: '3.00x',
            game: '< 33.00',
            count: '5.23',
            profit: '-0.00000002',
            key: '3'
          }];
        return(
            <div style={{background: "#ffffff"}}>
                <div className="betLists">
                    <Tabs defaultActiveKey="1">
                        <TabPane tab="" key="1">
                            <Table pagination={false} columns={columns} dataSource={data} />
                        </TabPane>
                        <TabPane tab="" key="2">
                            <Table pagination={false} columns={columns} dataSource={data} />
                        </TabPane>
                        <TabPane tab="" key="3">
                            <Table pagination={false} columns={columns} dataSource={data} />
                        </TabPane>
                    </Tabs>
                </div>
            </div>
        )
    }
}
export default BetLists;


