import React, { Component } from 'react';
import {Modal , Button, Input ,Spin} from 'antd';
import './Equity.less';
import Pubsub from 'pubsub-js';
import {
    Link
} from 'react-router-dom'
class Equity extends Component {
    constructor(props) {
        super(props)
        this.state = {
            sendVisible: false,
            newUserSeed: '39012fsfsf',
            newServerSeed: 'e53840cbbdf86f738acc18cdf47cc77981e824744d5fd11992358ebccb55adcb',
            userSeend: '13228570930612',
            newSha256: 'e53840cbbdf86f738acc18cdf47cc77981e824744d5fd11992358ebccb55adcb',
            serverSeed: 'a013a7f32b407e545f81d85223b76417be849670580c9be23c91862ae5e40950',
            sha256: '1f9f437673ef82aadaeb6c81e9af192b5642581982869fb222dff0a8cfe1cf6d',
            seed: '39012fsfsf',
            betId: ''
        }
        this.setSeedVisible = this.setSeedVisible.bind(this);
        this.handleChangeUserSeend = this.handleChangeUserSeend.bind(this);
        this.handleChangeBetId = this.handleChangeBetId.bind(this);
        this.showBetDetail = this.showBetDetail.bind(this);
    }
    setSeedVisible(sendVisible) {
        this.setState({ sendVisible });
    }
    
    handleChangeUserSeend(sendVisible, newUserSeed) {
        this.setState({ newUserSeed });
        this.setState({ sendVisible });
    }
    handleChangeBetId(betId) {
        this.setState({betId})
    }
    showBetDetail() {
        Pubsub.publish('BetDetail', this.state.betId)
    }
    render() {
        const {equityVisible, setEquityVisible} = this.props;
        const {sendVisible, newUserSeed, newServerSeed, newSha256, userSeend, serverSeed, sha256, seed, betId} = this.state
        return(
            <Modal
                title=""
                wrapClassName="equityModal"
                visible={equityVisible}
                footer={null}
                onOk={() => setEquityVisible(false)}
                onCancel={() => setEquityVisible(false)}
                >
                <p>Diceroller<Link to="/verify"><span onClick={() => setEquityVisible(false)}>...</span></Link></p>
                <div style={{textAlign:"center",margin: "10px"}}>
                <Button type="primary" onClick={() => this.setSeedVisible(true)}></Button>
                </div>
                <h2></h2>
                <h3></h3>
                <p>{newUserSeed}</p>
                <h3>sha-256</h3>
                <p>{newSha256}</p>
                <h3></h3>
                <p>0</p>
                <h2></h2>
                <h3></h3>
                <p>{userSeend}</p>
                <h3></h3>
                <p>{serverSeed}</p>
                <h3>sha-256</h3>
                <p>{sha256}</p>
                <h2></h2>
                <p></p>
                <div style={{textAlign:"center",margin: "10px"}}>
                <Input placeholder="Bet ID (without #)" value={betId} onChange={(e) => this.handleChangeBetId(e.target.value)}/>
                <Button type="primary" onClick={this.showBetDetail}></Button>
                </div>
                <Modal
                  title=""
                  wrapClassName="seedModal"
                  visible={sendVisible}
                  onOk={() => this.handleChangeUserSeend(false, this.state.seed)}
                  onCancel={() => this.setSeedVisible(false)}
                    >
                    <h3>()</h3>
                    <p>e53840cbbdf86f738acc18cdf47cc77981e824744d5fd11992358ebccb55adcb</p>
                    <h3></h3>
                    <Input placeholder="Basic usage" value={seed} onChange={(e) => this.setState({seed: e.target.value})}/>
                </Modal>
            </Modal>
        )
    }
}
export default Equity;


